.Self-Block {
    padding: 10px;
    margin: 30px;
    max-width: 55%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.Intro {
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 10px;
    align-items: center;
}

.Subsequent-Text {
    padding: 1px;
    margin: 1px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.useless {
    flex-grow: 1;
    padding: 10px;
}

.face-img {
    max-width:80%;
    height: auto;
    margin: 10px;
}

.face {
    flex-grow: 1;
}

.face-text {
    flex-grow: 1;
    padding: 1px;
    margin: 1px;
    max-width: 50%;
}