body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Self-Block {
    padding: 10px;
    margin: 30px;
    max-width: 55%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

.Intro {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 10px;
    -webkit-align-items: center;
            align-items: center;
}

.Subsequent-Text {
    padding: 1px;
    margin: 1px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

.useless {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    padding: 10px;
}

.face-img {
    max-width:80%;
    height: auto;
    margin: 10px;
}

.face {
    -webkit-flex-grow: 1;
            flex-grow: 1;
}

.face-text {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    padding: 1px;
    margin: 1px;
    max-width: 50%;
}
.header {
  background-color: #C96567;
  min-height: 5vh;
  padding: 7px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: baseline;
          align-items: baseline;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #314455;
}

.header-name {
    margin: 10px;
    padding-left: 10px;
    font-size: 2vw;
}

.header-link-divider {
    margin: 5px;
}

.header-links {
    font-size: 1.5vw;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    margin: 3px;
}

.header-link {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    padding: 5px;
}

a:link {
    color: #314455;
  }
  
  /* visited link */
  a:visited {
    color: #314455;
  }
  
  /* mouse over link */
  a:hover {
    color: rgb(252, 238, 238);
  }
  
  /* selected link */
  a:active {
    color: black;
  }
.App {
  text-align: center;
  background-color: #314455;
}

.main-content {
  display: -webkit-flex;
  display: flex;
  padding: 10px;
  margin: 5px;
  background-color: #314455;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  color: rgb(252, 238, 238);
}

.App-link {
  color: #61dafb;
}



.footer {
    left: 0;
    bottom: 0;
    padding: 10px;
    width: 100%;
    background-color: #97AABD;
    color: #314455;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
  }

.footer-link {
    padding: 10px;
}

a:link {
    color: #314455;
  }
  
  /* visited link */
  a:visited {
    color: #314455;
  }
  
  /* mouse over link */
  a:hover {
    color: rgb(252, 238, 238);
  }
  
  /* selected link */
  a:active {
    color: black;
  }
