.footer {
    left: 0;
    bottom: 0;
    padding: 10px;
    width: 100%;
    background-color: #97AABD;
    color: #314455;
    text-align: center;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }

.footer-link {
    padding: 10px;
}

a:link {
    color: #314455;
  }
  
  /* visited link */
  a:visited {
    color: #314455;
  }
  
  /* mouse over link */
  a:hover {
    color: rgb(252, 238, 238);
  }
  
  /* selected link */
  a:active {
    color: black;
  }